@import "../../variables";

.AboutMe {
  background: #FFFFFF;
  border-radius: 12px;
  padding: 24px;

  display: flex;
  flex-direction: column;
  gap: 32px;

  &Body {
    display: grid;
    grid-template-columns: 3fr 1fr;
    gap: 32px;
    color: $font-color;

    & strong {
      font-weight: 600;
      color: $font-color;
    }

    & h2 {
      margin: 0;
      font-size: 24px;
      color: $font-color;
      line-height: 36px;
      font-weight: 400;
    }

    & p {
      font-size: 20px;
      line-height: 28px;
      font-weight: 400;
      color: $font-color;
    }

    &Pic {
      width: 100%;
      height: auto;
    }
  }

  &Footer {
    display: flex;
    gap: 24px;
  }
}
